.center-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80vh;
  }
  
  .big-text {
    font-size: 3rem;
    text-align: center;
  }

  .middle-text {
    font-size: 1rem;
    text-align: center;
  }

  .signup-button {
    width: 20%;
    height: 10vh;
    background-color: lightgreen;
    font-size: 2.5em;
    font-weight: bold;
    color: white;
    display: block;
    margin: 0 auto;
  }